import React, { useEffect, useState } from 'react';
import OnboardingForm, { OnboardingData } from '../OnboardingForm';

const STORAGE_KEY = 'airia_onboarding_data';

export const DashboardOnboardingPanel: React.FC = () => {
  const [completedAt, setCompletedAt] = useState<string | null>(null);

  useEffect(() => {
    try {
      const saved = localStorage.getItem(STORAGE_KEY);
      if (!saved) return;
      const parsed = JSON.parse(saved) as Partial<OnboardingData>;
      if (parsed.completedAt) {
        setCompletedAt(parsed.completedAt);
      }
    } catch {
      // ignore
    }
  }, []);

  const handleComplete = (data: OnboardingData) => {
    setCompletedAt(data.completedAt || null);
  };

  return (
    <div style={{ height: '100%', display: 'flex', flexDirection: 'column', gap: 10, overflow: 'hidden' }}>
      <div className="dash-row">
        <div className="dash-muted">自己理解の質問（4ステップ）</div>
        <div className="small-pill">
          {completedAt ? `完了: ${new Date(completedAt).toLocaleDateString('ja-JP')}` : '未完了'}
        </div>
      </div>

      <div style={{ flex: 1, minHeight: 0, overflow: 'hidden' }}>
        <OnboardingForm onComplete={handleComplete} />
      </div>
    </div>
  );
};
