import React, { useMemo, useState } from 'react';
import { useAlbums } from '../../contexts/AlbumContext';
import Phase1SessionUI from '../../App';
import { DashboardOnboardingPanel } from './DashboardOnboardingPanel';
import { DashboardGalleryPanel } from './DashboardGalleryPanel';
import { DashboardAlbumPanel } from './DashboardAlbumPanel';
import { DashboardMusicPanel } from './DashboardMusicPanel';
import './Dashboard.css';

type LeftTab = 'create' | 'onboarding';
type CreateTab = 'session' | 'local' | 'external';
type RightTab = 'album' | 'music';

const TabButton: React.FC<{
  active: boolean;
  onClick: () => void;
  children: React.ReactNode;
  ariaLabel?: string;
}> = ({ active, onClick, children, ariaLabel }) => (
  <button
    type="button"
    className={`tab ${active ? 'active' : ''}`}
    onClick={onClick}
    role="tab"
    aria-selected={active}
    aria-label={ariaLabel}
  >
    {children}
  </button>
);

export const Dashboard: React.FC = () => {
  const { albums } = useAlbums();
  const musicAlbums = useMemo(() => albums.filter((a) => !!a.musicData), [albums]);

  const [leftTab, setLeftTab] = useState<LeftTab>('create');
  const [createTab, setCreateTab] = useState<CreateTab>('session');
  const [rightTab, setRightTab] = useState<RightTab>('album');

  return (
    <div className="dashboard-shell">
      <div className="dashboard-grid">
        {/* Left: Create / Onboarding */}
        <div className="dash-panel left">
          <div className="panel-header">
            <div className="panel-header-left">
              <div className="brand">AIRIA BEYOND</div>
              <div className="panel-subtitle">
                {albums.length} albums ・ {musicAlbums.length} tracks
              </div>
            </div>
            <div className="small-pill">PC</div>
          </div>

          <div className="panel-body">
            <div className="tabbar" role="tablist" aria-label="Left tabs">
              <TabButton active={leftTab === 'create'} onClick={() => setLeftTab('create')}>
                Create
              </TabButton>
              <TabButton active={leftTab === 'onboarding'} onClick={() => setLeftTab('onboarding')}>
                Onboarding
              </TabButton>
            </div>

            {leftTab === 'create' ? (
              <div
                className="section-card"
                style={{ flex: 1, minHeight: 0, overflow: 'hidden', display: 'flex', flexDirection: 'column', gap: 12 }}
              >
                <div className="tabbar" role="tablist" aria-label="Create tabs">
                  <TabButton active={createTab === 'session'} onClick={() => setCreateTab('session')}>
                    Session
                  </TabButton>
                  <TabButton active={createTab === 'local'} onClick={() => setCreateTab('local')}>
                    Local
                  </TabButton>
                  <TabButton active={createTab === 'external'} onClick={() => setCreateTab('external')}>
                    External
                  </TabButton>
                </div>

                <div style={{ flex: 1, minHeight: 0, overflow: 'hidden' }}>
                  <Phase1SessionUI variant="dashboard" dashboardTab={createTab} />
                </div>
              </div>
            ) : (
              <div className="section-card" style={{ flex: 1, minHeight: 0, overflow: 'hidden' }}>
                <DashboardOnboardingPanel />
              </div>
            )}
          </div>
        </div>

        {/* Center: Gallery */}
        <div className="dash-panel center">
          <div className="panel-header">
            <div className="panel-header-left">
              <div className="panel-title">Gallery</div>
              <div className="panel-subtitle">shelf / grid ・ page-based</div>
            </div>
          </div>

          <div className="panel-body">
            <DashboardGalleryPanel />
          </div>
        </div>

        {/* Right: Album / Music */}
        <div className="dash-panel right">
          <div className="panel-header">
            <div className="panel-header-left">
              <div className="panel-title">{rightTab === 'album' ? 'Album' : 'Music'}</div>
              <div className="panel-subtitle">selection ・ playback</div>
            </div>
          </div>

          <div className="panel-body">
            <div className="tabbar" role="tablist" aria-label="Right tabs">
              <TabButton active={rightTab === 'album'} onClick={() => setRightTab('album')}>
                Album
              </TabButton>
              <TabButton active={rightTab === 'music'} onClick={() => setRightTab('music')}>
                Music
              </TabButton>
            </div>

            <div className="section-card" style={{ flex: 1, minHeight: 0, overflow: 'hidden' }}>
              {rightTab === 'album' ? <DashboardAlbumPanel /> : <DashboardMusicPanel />}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
