import React, { useMemo } from 'react';
import { useAlbums } from '../../contexts/AlbumContext';
import { PlaybackState, useMusicPlayer } from '../../contexts/MusicPlayerContext';

const formatDuration = (sec?: number) => {
  if (!sec || sec <= 0) return '—';
  const m = Math.floor(sec / 60);
  const s = Math.floor(sec % 60);
  return `${m}:${s.toString().padStart(2, '0')}`;
};

export const DashboardAlbumPanel: React.FC = () => {
  const { albums, selectedAlbumId } = useAlbums();
  const mp = useMusicPlayer();

  const selected = useMemo(() => albums.find((a) => a.id === selectedAlbumId) || null, [albums, selectedAlbumId]);
  const musicAlbums = useMemo(() => albums.filter((a) => !!a.musicData), [albums]);

  if (!selected) {
    return (
      <div style={{ height: '100%', display: 'flex', flexDirection: 'column', justifyContent: 'center', gap: 10 }}>
        <div className="dash-muted">ギャラリーでアルバムを選択すると、ここに詳細が表示されます。</div>
        <div className="small-pill" style={{ width: 'fit-content' }}>Select an album</div>
      </div>
    );
  }

  const isCurrent = mp.state.currentAlbum?.id === selected.id;
  const isPlaying = mp.state.playbackState === PlaybackState.PLAYING;

  const handlePlayToggle = () => {
    if (!selected.musicData) return;

    // Ensure the queue contains music albums (stable order: context already newest-first)
    const index = Math.max(0, musicAlbums.findIndex((a) => a.id === selected.id));
    mp.setQueue(musicAlbums, index);
    mp.setCurrentAlbumFull(selected);

    if (isCurrent && isPlaying) {
      mp.pause();
    } else {
      mp.play();
    }
  };

  const mood = selected.mood || selected.title || 'Untitled';
  const created = selected.createdAt ? new Date(selected.createdAt).toLocaleString('ja-JP') : '—';
  const duration = formatDuration(selected.musicMetadata?.duration);
  const v = selected.metadata?.valence;
  const a = selected.metadata?.arousal;

  return (
    <div style={{ height: '100%', display: 'flex', flexDirection: 'column', gap: 12, overflow: 'hidden' }}>
      <div className="album-preview">
        <div className="album-thumb">
          {selected.imageDataURL ? (
            <img src={selected.imageDataURL} alt={mood} />
          ) : (
            <div style={{ width: '100%', height: '100%' }} />
          )}
        </div>

        <div style={{ minWidth: 0 }}>
          <h3 className="kv-title" title={mood} style={{ whiteSpace: 'nowrap', overflow: 'hidden', textOverflow: 'ellipsis' }}>{mood}</h3>
          <div className="meta-line">{created}</div>
          <div className="kv-meta">
            <span className="small-pill">duration {duration}</span>
            {typeof v === 'number' && <span className="small-pill">valence {v.toFixed(2)}</span>}
            {typeof a === 'number' && <span className="small-pill">arousal {a.toFixed(2)}</span>}
          </div>
        </div>
      </div>

      <div className="section-card" style={{ padding: 10, overflow: 'hidden' }}>
        <div className="dash-row">
          <div className="dash-muted">Music</div>
          {selected.musicData ? (
            <button type="button" className="btn btn-primary" onClick={handlePlayToggle}>
              {isCurrent && isPlaying ? 'Pause' : 'Play'}
            </button>
          ) : (
            <div className="small-pill">no track</div>
          )}
        </div>
        {selected.musicMetadata?.motif && (
          <div className="dash-muted" style={{ marginTop: 8 }}>
            Motif: <span style={{ color: 'rgba(0,0,0,0.78)' }}>{selected.musicMetadata.motif}</span>
          </div>
        )}
      </div>

      <div className="section-card" style={{ flex: 1, overflow: 'hidden' }}>
        <div className="dash-muted" style={{ marginBottom: 8 }}>Tags</div>
        <div style={{ display: 'flex', flexWrap: 'wrap', gap: 8 }}>
          {(selected.musicMetadata?.tags || selected.metadata?.tags || []).slice(0, 10).map((t) => (
            <span key={t} className="small-pill">{t}</span>
          ))}
          {(!selected.musicMetadata?.tags && !selected.metadata?.tags) && (
            <span className="dash-muted">—</span>
          )}
        </div>
      </div>
    </div>
  );
};
