import React, { useEffect, useMemo, useState } from 'react';
import { useAlbums } from '../../contexts/AlbumContext';
import BookshelfCanvas from '../gallery/BookshelfCanvas';
import GridView from '../gallery/fallback/GridView';
import EmptyState from '../visual/feedback/EmptyState';

type SortMode = 'time' | 'mood' | 'duration';
type ViewMode = '3d' | 'grid';

export const DashboardGalleryPanel: React.FC = () => {
  const { albums, selectAlbum } = useAlbums();

  const [constellationEnabled, setConstellationEnabled] = useState(true);
  const [sortMode, setSortMode] = useState<SortMode>('time');
  const [viewMode, setViewMode] = useState<ViewMode>('3d');
  const [page, setPage] = useState(0);

  useEffect(() => {
    setPage(0);
  }, [sortMode, viewMode, albums.length]);

  const sortedAlbums = useMemo(() => {
    const copy = [...albums];
    switch (sortMode) {
      case 'time':
        return copy; // already newest-first
      case 'mood':
        return copy.sort((a, b) => {
          const va = a.metadata?.valence ?? 0;
          const vb = b.metadata?.valence ?? 0;
          return vb - va;
        });
      case 'duration':
        return copy.sort((a, b) => {
          const da = a.musicMetadata?.duration ?? 0;
          const db = b.musicMetadata?.duration ?? 0;
          return db - da;
        });
      default:
        return copy;
    }
  }, [albums, sortMode]);

  const handleAlbumClick = (albumId: string) => {
    selectAlbum(albumId);
  };

  const pageSize = 6;
  const totalPages = Math.max(1, Math.ceil(sortedAlbums.length / pageSize));
  const safePage = Math.min(page, totalPages - 1);
  const pageAlbums = sortedAlbums.slice(safePage * pageSize, safePage * pageSize + pageSize);

  return (
    <>
      <div className="dash-row" style={{ gap: 10 }}>
        <div style={{ display: 'flex', gap: 10, alignItems: 'center' }}>
          <div className="tabbar" role="tablist" aria-label="View mode">
            <button
              type="button"
              className={`tab ${viewMode === '3d' ? 'active' : ''}`}
              onClick={() => setViewMode('3d')}
            >
              Shelf
            </button>
            <button
              type="button"
              className={`tab ${viewMode === 'grid' ? 'active' : ''}`}
              onClick={() => setViewMode('grid')}
            >
              Grid
            </button>
          </div>

          <div className="tabbar" role="tablist" aria-label="Sort mode">
            <button
              type="button"
              className={`tab ${sortMode === 'time' ? 'active' : ''}`}
              onClick={() => setSortMode('time')}
            >
              Time
            </button>
            <button
              type="button"
              className={`tab ${sortMode === 'mood' ? 'active' : ''}`}
              onClick={() => setSortMode('mood')}
            >
              Mood
            </button>
            <button
              type="button"
              className={`tab ${sortMode === 'duration' ? 'active' : ''}`}
              onClick={() => setSortMode('duration')}
            >
              Long
            </button>
          </div>
        </div>

        <label className="dash-muted" style={{ display: 'flex', alignItems: 'center', gap: 8 }}>
          <input
            type="checkbox"
            checked={constellationEnabled}
            onChange={() => setConstellationEnabled((v) => !v)}
            disabled={viewMode !== '3d'}
          />
          Constellation
        </label>
      </div>

      <div className="dash-gallery-stage">
        {albums.length === 0 ? (
          <div style={{ padding: 18 }}>
            <EmptyState
              title="まだアルバムがありません"
              description="左のCreateからセッションを開始すると、ここにアルバムが並びます。"
              actionLabel="Createへ"
              onAction={() => {
                // Create panel is always visible in the dashboard
                const el = document.querySelector('.dash-panel.left');
                if (el instanceof HTMLElement) el.focus?.();
              }}
              icon={<span className="empty-icon-shape" aria-hidden="true" />}
            />
          </div>
        ) : viewMode === '3d' ? (
          <BookshelfCanvas
            albums={sortedAlbums}
            onBookClick={handleAlbumClick}
            constellationEnabled={constellationEnabled}
            height="100%"
          />
        ) : (
          <div style={{ height: '100%', overflow: 'hidden' }}>
            <GridView albums={pageAlbums} onAlbumClick={handleAlbumClick} />
          </div>
        )}
      </div>

      {viewMode === 'grid' && albums.length > 0 && totalPages > 1 && (
        <div className="pager">
          <button
            type="button"
            className="pager-btn"
            onClick={() => setPage((p) => Math.max(0, p - 1))}
            disabled={safePage === 0}
          >
            Prev
          </button>
          <div className="pager-meta">
            {safePage + 1} / {totalPages}
          </div>
          <button
            type="button"
            className="pager-btn"
            onClick={() => setPage((p) => Math.min(totalPages - 1, p + 1))}
            disabled={safePage >= totalPages - 1}
          >
            Next
          </button>
        </div>
      )}
    </>
  );
};
