import React, { useMemo, useState } from 'react';
import { useAlbums } from '../../contexts/AlbumContext';
import { PlaybackState, useMusicPlayer } from '../../contexts/MusicPlayerContext';

const formatDuration = (sec?: number) => {
  if (!sec || sec <= 0) return '—';
  const m = Math.floor(sec / 60);
  const s = Math.floor(sec % 60);
  return `${m}:${s.toString().padStart(2, '0')}`;
};

export const DashboardMusicPanel: React.FC = () => {
  const { albums, selectAlbum } = useAlbums();
  const mp = useMusicPlayer();

  const musicAlbums = useMemo(() => albums.filter((a) => !!a.musicData), [albums]);
  const [page, setPage] = useState(0);

  const pageSize = 6;
  const totalPages = Math.max(1, Math.ceil(musicAlbums.length / pageSize));
  const safePage = Math.min(page, totalPages - 1);
  const items = musicAlbums.slice(safePage * pageSize, safePage * pageSize + pageSize);

  const isPlaying = mp.state.playbackState === PlaybackState.PLAYING;

  const handlePlayIndex = (albumId: string) => {
    const idx = Math.max(0, musicAlbums.findIndex((a) => a.id === albumId));
    const album = musicAlbums[idx];
    if (!album) return;

    mp.setQueue(musicAlbums, idx);
    mp.setCurrentAlbumFull(album);
    mp.play();
    selectAlbum(albumId);
  };

  if (musicAlbums.length === 0) {
    return (
      <div style={{ height: '100%', display: 'flex', flexDirection: 'column', justifyContent: 'center', gap: 10 }}>
        <div className="dash-muted">まだ音楽付きアルバムがありません。</div>
        <div className="dash-muted">左のCreate → External/Local で生成すると、ここに追加されます。</div>
      </div>
    );
  }

  return (
    <div style={{ height: '100%', display: 'flex', flexDirection: 'column', overflow: 'hidden' }}>
      <div className="dash-row" style={{ marginBottom: 10 }}>
        <div className="dash-muted">Queue</div>
        <div className="small-pill">{isPlaying ? 'playing' : 'ready'}</div>
      </div>

      <div className="compact-list" style={{ flex: 1, minHeight: 0, overflow: 'hidden' }}>
        {items.map((a) => {
          const title = a.mood || a.title || 'Untitled';
          const dur = formatDuration(a.musicMetadata?.duration);
          const isCurrent = mp.state.currentAlbum?.id === a.id;

          return (
            <div
              key={a.id}
              className="compact-item"
              onClick={() => handlePlayIndex(a.id)}
              role="button"
              tabIndex={0}
              onKeyDown={(e) => {
                if (e.key === 'Enter' || e.key === ' ') handlePlayIndex(a.id);
              }}
              aria-label={`Play ${title}`}
            >
              <div className="compact-item-thumb">
                {a.imageDataURL ? <img src={a.imageDataURL} alt={title} /> : <div />}
              </div>
              <div style={{ minWidth: 0 }}>
                <div className="compact-item-title" style={{ whiteSpace: 'nowrap', overflow: 'hidden', textOverflow: 'ellipsis' }}>
                  {title}
                </div>
                <div className="compact-item-sub">{dur}</div>
              </div>
              <div className="compact-item-action">{isCurrent ? (isPlaying ? 'Now' : 'Resume') : 'Play'}</div>
            </div>
          );
        })}
      </div>

      {totalPages > 1 && (
        <div className="pager">
          <button
            type="button"
            className="pager-btn"
            onClick={() => setPage((p) => Math.max(0, p - 1))}
            disabled={safePage === 0}
          >
            Prev
          </button>
          <div className="pager-meta">
            {safePage + 1} / {totalPages}
          </div>
          <button
            type="button"
            className="pager-btn"
            onClick={() => setPage((p) => Math.min(totalPages - 1, p + 1))}
            disabled={safePage >= totalPages - 1}
          >
            Next
          </button>
        </div>
      )}
    </div>
  );
};
